import uPopup from './u-popup'
export default {
	props: {
		safeAreaInsetBottom: {
			type: Boolean,
			default: false
		},
		// 是否允许通过点击遮罩关闭Picker
		maskCloseAble: {
			type: Boolean,
			default: true
		},
		// 通过双向绑定控制组件的弹出与收起
		value: {
			type: Boolean,
			default: false
		},
	},
	data() {
		return {
			pickVal:[0, 0, 0],
			districtsObj: {
				provinces: [],
				cities: [],
				areas: [],
			},
			province: 0,
			city: 0,
			area: 0
		}
	},
	watch: {
		// 如果地区发生变化，为了让picker联动起来，必须重置this.citys和this.areas
		province(val) {
			this.loadCities(this.districtsObj.provinces[this.province].AreaId);
		},
		city(val) {
			this.loadAreas(this.districtsObj.cities[this.city].AreaId);
		}
	},
	mounted() {
		this.loadDistrict()
	},
	methods: {
		close() {
			this.$emit('input', false);
		},
		async loadDistrict() {
			this.loadProvinces()
		},
		loadProvinces() { // 加载省份
			uni.request({
				url: 'https://test-api.tiananhub.com/api/province/GetListProvince',
				method: 'get',
				success: async (res) => {
					let {data} = res.data
					this.districtsObj.provinces = data
					this.loadCities(data[0].AreaId)
				},
				fail:async(res) => {
				}
			})
		},
		loadCities(AreaId) {
			uni.request({
				url: 'https://test-api.tiananhub.com/api/province/GetListCity',
				data: {
					AreaId
				},
				method: 'get',
				success: async (res) => {
					let {data} = res.data
					this.districtsObj.cities = data
					this.loadAreas(data[0].AreaId)
				},
				fail:async(res) => {
				}
			})
		},
		loadAreas(AreaId) {
			uni.request({
				url: 'https://test-api.tiananhub.com/api/province/GetListCity',
				data: {
					AreaId
				},
				method: 'get',
				success: async (res) => {
					let {data} = res.data
					this.districtsObj.areas = data
				},
				fail:async(res) => {
				}
			})
		},
		bindChange(event) {
			this.pickVal = event.detail.value;
			let i = 0;
			this.province = this.pickVal[i++];
			this.city = this.pickVal[i++];
			this.area = this.pickVal[i++];
		},
		getResult(event = null) {
			let result = {
				province: this.districtsObj.provinces[this.province],
				city: this.districtsObj.cities[this.city],
				area: this.districtsObj.areas[this.area],
			}
			if (event) this.$emit(event, result);
			this.close();
		}
	},
	components:{
		uPopup
	}
}
